const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			mobile: '',
			currentTime: 60, // 倒计时初始值
			time: '获取验证码',
			noShow: 1,
			verification_key: '',
			mobileCode: ''
		}
	},
	onLoad(e) {
		console.log(e)
		console.log(this.mobileCode)
		this.mobile = e.num
	},
	methods: {
		inputValue(e){
			console.log(e)
			this.mobileCode = e.detail.value
		},
		goMyMobile(e){
			let url = e.target.dataset.name
			if(this.mobileCode == ""){
				uni.showToast({
					title: '请获取验证码',
					icon: 'none'
				})
			}else{
				uni.request({
					url: `${helper.requestUrl}/user/old-mobile-verification`,
					method: 'POST',
					header: {
						authorization: app.globalData.token
					},
					data: {
						code: this.mobileCode,
						verification_key: this.verification_key
					},
					success: (res) => {
						console.log(res);
						uni.hideLoading();
						res = helper.null2str(res)
						if (res.statusCode == 200) {
							uni.navigateTo({
								url: `/pages/${url}`
							})
						} else {
							uni.showToast({
								title: res.data.message
							});
						}
				
					}
				});
			}
			
			// uni.navigateTo({
			// 	url: `/pages/${url}`
			// })
		},
		getCode(){
			uni.request({
				url: `${helper.requestUrl}/send_sms`,
				method: 'POST',
				data: {
					mobile: this.mobile
				},
				success: (res) => {
					console.log(res);
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.statusCode == 200) {
						this.verification_key = res.data.key
						this.countdown();
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
			
				}
			});
		},
		//倒计时
		countdown() {
			var currentTime = this.currentTime;
			this.time = `倒计时${currentTime}秒`
			var interval = setInterval(() => {
				this.time = '倒计时' + (currentTime - 1) + '秒'
				currentTime--
				if (currentTime <= 0) {
					clearInterval(interval)
					this.time = '重新获取'
					this.currentTime = 60
					this.noShow = 1
				} else if(currentTime > 0){
					this.noShow = 0
				}
				
			}, 1000)
		},
	}
}
